/* header */

.logo {
  width: 70%;
}
.logo img {
  width: 18%;
}
.custom-navbar ul {
  width: 100%;
  margin: 0px !important;
}
li a {
  position: relative;
}
li a:hover::after {
  background: #ffaa5f;
  height: 2.5px;
  width: 48px;
  content: "";
  position: absolute;
  bottom: 0;
  left: calc(50% - 24px);
}
li:last-child a:hover::after {
  width: 60px;
  left: calc(50% - 30px);
}
li a.active-link::after {
  background: #d46300;
  height: 2.5px;
  width: 48px;
  content: "";
  position: absolute;
  bottom: 0;
  left: calc(50% - 24px);
}
li:last-child a.active-link::after {
  width: 60px;
  left: calc(50% - 30px);
}

/* homepage */
img.profile {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  object-fit: cover;
}
.homepage-container > div {
  width: 50%;
}

/* contact */
.form {
  width: 35%;
}
.form-buttons {
  width: 45%;
}
input[type="submit"]:hover,
input[type="reset"]:hover,.download-btn:hover {
  background-color: #c95e00 !important;
}
a {
  text-decoration: none;
  color: #fc7500;
}
.address p {
  color: #fc7500;
}
/* about */
.html{
  width: 35%;
}
.css{
  width: 30%;
}
.bootstrap{
  width: 45%;
}
.tailwind{
  width: 70%;
}
.js{
  width: 45%;
}
.react{
  width: 40%;
}
.figma{
  width: 30%;
}
.sass{
  width: 40%;
}
/* work */
.width {
  width: 70%;
}
code {
  color: #ff7700;
}
.custom-border {
  border: 2px solid #e36a00;
  cursor: pointer;
}
.web-box {
  border: 2px solid #e36a00;
  position: relative;
  display: inline-block;
  height: 100%;
  width: 100%;
  cursor: pointer;
}
.web-box::after {
  width: 100%;
  height: 100%;
  background-color: #e36a00d3;
  position: absolute;
  content: "CLICK HERE";
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
  top: 0;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}
.web-box:hover::after {
  opacity: 1;
}
.popup-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.arrow {
  position: absolute;
  top: 50%;
  z-index: 1;
  cursor: pointer;
}

/* footer */
.footer-div p {
  margin-bottom: 0px;
}
.footer-div div {
  width: 10%;
}

@media screen and (max-width: 992px) {
  .custom-navbar {
    position: absolute;
    top: 7px;
    right: 75px;
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px 0;
    background: #e9ecef;
    border: 1.5px solid #e77817f8;
  }
  .custom-navbar ul {
    width: 50%;
  }
  li a.active-link::after {
    left: 0;
  }
  li:last-child a.active-link::after {
    left: 0;
  }
  .homepage-container > div:first-child {
    width: 60%;
  }
  .homepage-container > div:last-child {
    width: 40%;
  }
  img.profile {
    height: 250px;
    width: 250px;
  }
  .custom-css{
    padding: 5px !important;
    font-size: 20px !important;
  }
  .form {
    width: 45%;
  }
  .form-buttons {
    width: 50%;
  }
}
@media screen and (max-width: 768px) {
  .homepage-container {
    flex-direction: column;
  }
  .homepage-container > div {
    width: 100% !important;
  }
  .homepage-container > div:first-child h1 {
    font-size: 30px;
  }
  .homepage-container > div:first-child h2 {
    font-size: 25px;
  }
  .homepage-container > div:first-child h2 span {
    font-size: 30px !important;
  }
  .homepage-container > div:first-child p {
    font-size: 18px;
  }
  .homepage-container > div:last-child {
    display: flex;
    justify-content: center;
  }
  .form {
    width: 60%;
  }
  .form-buttons {
    width: 50%;
  }
  .footer-div div {
    width: 15%;
  }
}
@media screen and (max-width: 576px) {
  .logo img {
    width: 25%;
  }
  .custom-navbar {
    width: 30%;
  }
  .homepage-container > div:first-child h1 {
    font-size: 25px;
  }
  .homepage-container > div:first-child h2 {
    font-size: 20px;
  }
  .homepage-container > div:first-child h2 span {
    font-size: 25px !important;
  }
  .homepage-container > div:first-child p {
    font-size: 15px;
  }
  .html{
    width: 45%;
  }
  .css{
    width: 45%;
  }
  .bootstrap{
    width: 50%;
  }
  .tailwind{
    width: 70%;
  }
  .js{
    width: 50%;
  }
  .react{
    width: 50%;
  }
  .figma{
    width: 40%;
  }
  .sass{
    width: 50%;
  }
  .form {
    width: 75%;
  }
  .custom-css{
    font-size: 14px !important;
    padding: 3px !important;
  }
  .form-buttons {
    width: 50%;
  }
  .footer-div {
    flex-direction: column;
    align-items: flex-start !important;
  }
  .footer-div div {
    width: 25%;
    margin-top: 10px;
  }
}
